import React from 'react';

import { Card } from "react-bootstrap";

import './card-style.css';

const ExpCard = props =>{
    return(
    <Card className="exp-card overflow" style={{ width: '18rem' }}>
        <Card.Img className="exp-card-img" variant="top" src= {props.Cardimg} />
        <Card.Body>
            <Card.Title>{props.title}</Card.Title>
            <Card.Text>
                <ul class="text-left">
                    <li>{props.expText[0]}</li>
                    <li>{props.expText[1]}</li>
                    <li>{props.expText[2]}</li>
                </ul>
            </Card.Text>
        </Card.Body>
    </Card>
    )
}

export default ExpCard;