
import React from 'react';
import { Navbar, Nav } from "react-bootstrap";
import Logo from '../Assets/logo-2.png';
import "./home-style.css";


const Navigation = props =>{
    return(
        <Navbar
            collapseOnSelect
            expand="lg"
            variant="dark"
            className="navigation shadow-lg navigation fixed h-100"
            sticky="top">

            <Navbar.Brand href={'/'}><div className="logo"><img src={Logo}></img></div></Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="ml-auto">
              <Navbar.Brand href={'/'}>Home</Navbar.Brand>
              <Nav.Link href={'/about'}>About</Nav.Link>
                <Nav.Link href={'/aards'}>Services</Nav.Link>
                <Nav.Link href={'/totalexperience'}>Experience</Nav.Link>
                <Nav.Link href={'/contact'}>Contact</Nav.Link>
              </Nav>
            </Navbar.Collapse>
        </Navbar>
    );
}

export default Navigation;