import React, { Component } from 'react';
import emailjs from 'emailjs-com';
import Dialog from 'react-bootstrap-dialog'


import './contact-style.css';
class Contact extends Component {

    sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('gmail', 'template_h7d337y', e.target, 'user_poSquJBhjIkeN6xFEXxPb')
          .then((result) => {
              console.log(result.text);

              this.dialog.showAlert("Successfully sent the mail... Thank You!!")
          }, (error) => {
              console.log(error.text);
              this.dialog.showAlert("Error. Please try after sometime.");
          });
        e.target.reset();
    }

    render() {
        return (
            <section>
                
                <div className="contact-component container-fluid">
                
                <div class="row">


                    <div className="col-md-8" >
                        <div class className="row p-4">
                            <div className ="contact-text text-center text-white pt-4">
                                <p>
                                    I would like to thank you for taking your valuable time to visit my page. 
                                    Please feel free to contact me if you have any questions.
                                </p>
                            </div>
                        </div>             
                    </div>
 

                    <div class="col-md-3 col-lg-3 col-sm-3 col-xs-3 pr-4  contact-form">   
                        <h3 className="text-center text-white p-3 m-3">Contact Me</h3>

                        <form id="submit-form" method="POST" onSubmit={this.sendEmail}>
                            <div class="form-group text-left">
                                <input type="text" id="name" name="name"  placeholder="Your Name" required></input>
                            </div>
                            <div class="form-group text-left">
                                <input type="email" id="email" name="email"  placeholder="Your Email-Id" required/>
                            </div>
                            <div class="form-group text-left">
                                <input type="subject" id="subject" name="subject"  placeholder="Subject" required/>
                            </div>
                            <div class="form-group text-left">
                                <textarea id="message" name="message" cols="41" rows="4"  placeholder="Type Your message" required></textarea>
                            </div>
                            <div className="m-3 text-left">
                                <button className="submit-btn">Submit</button>
                            </div>
                        </form>
                        <Dialog ref={(component) => { this.dialog = component }} />
                    </div>

                </div>
            </div>
        </section>
        )
    }
}

export default Contact;
