import React from 'react';

import './exp-style.css';


    
const Experience = props =>{

      return (
        <div>
          <div className="row">
            <div className="text-center col-md-2 exp-head">
              <h6>
                {props.experience.year}
              </h6>
            </div>
            <div className="col-md-5">
              <ul class="text-left exp-list">
                <div className="exp-role">
                  <li>{props.experience.position} </li>
                </div>
                <div className="exp-company">
                  <li>{props.experience.company}, {props.experience.location}</li>
                </div>
              </ul>
            </div>
          </div>
      </div>
    )
}

export default Experience;
