import React, { Component } from 'react'
import Civilimg from '../Assets/civil-engg.jpg';
import Mgmtimg from '../Assets/proj-mgmt.jpg';
import Adminimg from '../Assets/contract-mgmt.jpg';

import ExpCard from './CardUI';

class Cards extends Component {

    render() {
 
        let projMgmtText = ["Collaborating with engineers, architects etc. to determine the specifications of the project",
                        "Negotiating contracts with external vendors to reach profitable agreements",
                        "Obtaining permits and licenses from appropriate authorities"];

        let civilEnggText = ["Managing, designing, developing, creating and maintaining construction projects",
                        "Conducting on-site investigations and analyzing data",
                        "Assessing potential risks, materials and costs"];


        let contractAdminText = ["Preparing sales and purchase contracts",
                        "Negotiating contract terms with internal and external partners",
                        "Reviewing and updating existing contracts"];
        return (
            <section>
                
                <div className="service-component container-fluid">
                <h3 className="text-center text-white pt-2">Services</h3>
                    <div className="row">
                        <div className="col-md-4 d-flex justify-content-center">
                            <ExpCard title="Project Management" Cardimg={Mgmtimg} expText={projMgmtText}/>
                        </div>
                        <div className="col-md-4 d-flex justify-content-center">
                            <ExpCard title="Civil Engineering" Cardimg={Civilimg} expText={civilEnggText}/>
                        </div>
                        <div className="col-md-4 d-flex justify-content-center">
                            <ExpCard title="Contract Administration" Cardimg={Adminimg} expText={contractAdminText}/>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default Cards;


