import React, { Component } from 'react';
import ProfilePic from '../Assets/sreejith-photo.jpg';
import "./home-style.css";


class HomePage extends Component {

    handleAbtClick = () => {
        this.props.history.push('/About');
    }

    handleContactClick = () => {
        this.props.history.push('/Contact');
    }

    render() {
        return (
            <section>
                <div className="home">
                
                    <div className="home-content row p-3 ">
                        <div className="col-md-4"></div>
                        <div className="col-md-4 text-center">
                            <div className="row ">
                                <img className="profile-pic mx-auto d-block rounded-circle" src={ProfilePic} alt=""/>
                            </div>
                            <div className="row text-white pt-3">
                                <h5>
                                    Hi I'm <i>Sreejith Babu</i>, a Project Engineer who is having a
                                    wide-range of experience in civil engineering projects.
                                </h5>
                            </div>
                        </div>
                        <div className="col-md-1"></div>
                        <div className="col-md-3">
                            <div className="container intro text-white text-center">
                                <div className="m-3">
                                    <button className="about-btn" onClick ={this.handleAbtClick}>ABOUT</button>
                                </div>
                                <div className="m-3">
                                    <button className="contact-btn" onClick ={this.handleContactClick}>CONTACT</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default HomePage;